iframe {
    display: none;
}

.overview-div {
    padding-top: 5px;
    padding-bottom: 5px;
}

.time-place {
    padding-left: 5px;
    padding-right: 5px;
    display: inline-block;
}

#overview {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#overview_current {
    display: -ms-flexbox;
    display: flex;
}

#overview_map {
    display: block;
    -ms-flex: 3 1 0%;
        flex: 3 1 0%;
}

#time_place_div {
    font-size: 20px;
    display: block;
    text-align: center;
}

#overview_current_data {
    -ms-flex: 2 1 0%;
        flex: 2 1 0%;
}

#overview_week {
    -ms-flex: 3 1 0%;
        flex: 3 1 0%;
    height: 100%;
}

@media screen and  (min-width: 721px) {
    iframe {
        display: block;
        margin: auto;
        max-width: 300px;
        max-height: 300px;
    }

    #overview_map {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
    }

    #time_place_div {
        -ms-flex: 2 1 0%;
            flex: 2 1 0%;
    }

    #map_wrapper {
        -ms-flex: 3 1 0%;
            flex: 3 1 0%;
    }
}



